import React, { useEffect, useState } from 'react';
import { useParams } from "react-router-dom";
import axios from 'axios';
import '../scss/Detail.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleRight, faAngleLeft, faList } from '@fortawesome/free-solid-svg-icons';

function Detail(props) {
    let { id } = useParams();
    let [sillok, setSillok] = useState({id:0 ,era:'' ,title:'' ,content:''});
    let [lastPage, setLastPage] = useState(0);


    useEffect(getAxiosById,[]);

    return (        
        <section className='detail'>
            <div className='inner'>
                <Content sillok={sillok}/>
                <div className='detail__btns' onClick={moveOnClick}>
                    <button className='go-back'>
                        {
                            id == 0
                            ? null
                            : <FontAwesomeIcon icon={faAngleLeft} id='go-back'/>
                        }
                    </button>
                    <button className='go-list'>
                        <FontAwesomeIcon icon={faList} id='go-list'/>
                    </button>
                    <button className='go-next'>
                        {
                            id == lastPage-1
                            ? null
                            : <FontAwesomeIcon icon={faAngleRight} id='go-next'/>
                        }
                    </button>
                </div>
            </div>
        </section>
    )

    function getAxiosById() {
        axios
        .get('https://atmosg.github.io/chung2sillok/sillok.json')
        .then(response => {
            setLastPage(response.data.length)
            let data = response.data.find(val => {
                return val.id === id    
            })
            setSillok(data)
        })
        .catch(error => console.log(error))
    }

    function btnDisabled() {
        if (parseInt(id) == 0 ) {
            console.log(document.getElementsByClassName('go-back').style)
        }
    }

    function moveOnClick(event) {
        let link = parseInt(id);
        switch (event.target.id) {
            case ('go-back'):
                window.location.replace(`/sillok/${link-1}`); break
            case ('go-next'):
                window.location.replace(`/sillok/${link+1}`); break
            case ('go-list'):
                window.location.replace(`/sillok`)
        }
    }
}

function Content(props) {
    return (
        <div className='detail__content'>
            <h1>{props.sillok.title}</h1>
            <p>{props.sillok.content}</p>
            <h2>{props.sillok.era}</h2>
        </div>
    )
}

export default Detail;